<template>
    <div>
        <div class="loginBox">
            <div class="login" v-show="loginShow">
                <div class="logo"><img src="../assets/logo.png" alt=""></div>
                <div class="loginOption">
                    <span :class="[isTrue?'active':'isActive']" @click="toggleBar('user')">用户登录</span>\<span :class="[!isTrue?'active':'isActive']" @click="toggleBar('admin')">管理员登录</span>
                </div>
                <div class="userLogin" v-show="showUser">
                    <el-form :model="uesrloginInfo" @keyup.enter.native="usersubmitForm('uesrloginInfo')" :rules="loginRules" class="demo-ruleForm" ref="uesrloginInfo">
                        <el-form-item label="" prop="username">
                            <el-input prefix-icon="el-icon-user" size="medium" type="text" v-model="uesrloginInfo.username" placeholder="手机号/账号"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="password">
                            <el-input size="medium" prefix-icon="el-icon-lock" type="password" v-model="uesrloginInfo.password" placeholder="密码"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="code">
                            <el-input size="medium" prop="code" v-model="uesrloginInfo.code" placeholder="验证码"></el-input>
                            <button size="medium" @click.prevent="getCode(uesrloginInfo)" class="code-btn" :disabled="!show">
                                <span v-show="show">发送验证码</span>
                                <span v-show="!show" class="count">{{count}} s</span>
                            </button>
                        </el-form-item>
                        <div class="forget"><span><el-checkbox v-model="checked">两周内自动登录</el-checkbox></span><span><el-button type="text">忘记密码</el-button>|<el-button type="text" @click="regist('reg')">注册</el-button></span></div>
                        <el-form-item style="width:100%;">
                            <el-button type="primary" style="width:100%;" @click.prevent="usersubmitForm('uesrloginInfo')">登录</el-button>
                        </el-form-item>
                    </el-form>
                </div>
                <div class="userLogin" v-show="!showUser">
                    <el-form :model="loginInfo" @keyup.enter.native="submitForm('loginInfo')" :rules="loginRules" class="demo-ruleForm" ref="loginInfo">
                        <el-form-item label="" prop="username">
                            <el-input prefix-icon="el-icon-user" size="medium" type="text" v-model="loginInfo.username" placeholder="手机号/账号"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="password">
                            <el-input size="medium" prefix-icon="el-icon-lock" type="password" v-model="loginInfo.password" placeholder="密码"></el-input>
                        </el-form-item>
                        <div class="forget"><span><el-checkbox v-model="checked">两周内自动登录</el-checkbox></span></div>
                        <el-form-item style="width:100%;">
                            <el-button type="primary" style="width:100%;" @click.prevent="submitForm('loginInfo')">登录</el-button>
                        </el-form-item>
                    </el-form>
                </div>
                <div class="weChat">
                    <p>第三方账户登录</p>
                    <span><img src="../assets/wechat.png" alt=""></span>
                </div>
            </div>
        </div>
        <div class="step">
            <p>申请调解步骤</p>
            <div class="stepxian"></div>
            <div class="stepImg"><img src="../assets/step.png" alt=""></div>
        </div>
        <div class="step news">
            <p>新闻动态</p>
            <div class="stepxian"></div>
            <div class="newsBottom">
                <div class="newsLeft">
                    <div class="newsImg"></div>
                    <div class="newsTitle">
                        <p>{{news.title}}<span>>></span></p>
                    </div>
                    <div class="newsCoent">
                        <div class="coentLeft"><h1>{{news.year}}</h1><p>{{news.date}}</p></div>
                        <div class="coentRight">{{news.remarks}}</div>
                    </div>
                </div>
                <div class="newsRight">
                    <div class="newsCoent" v-for="(item, index) in newsArr" :key='index'>
                        <div class="coentLeft"><h1>{{item.year}}</h1><p>{{item.date}}</p></div>
                        <div class="coentRight">
                            <p>{{item.title}}</p>
                            <span>{{item.remarks}}</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="step">
            <p>服务内容</p>
            <div class="stepxian"></div>
            <div class="newsBottom">
                <swiper></swiper>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
import Qs from 'qs'
import swiper from '@/components/swiperService'
import {getCodes, getLogin, getUserLogin, getUserNotify} from '@/request'
import {ERR_OK} from '@/request/config'
const TIME_COUNT = 60 // 倒计时的时间
export default {
    components: {
        swiper
    },
    data() {
        var funChina = (rule, value, callback) => {
            if (!value) {
                return callback(new Error('不可为空'))
            } else {
                const regs = /^[0-9a-zA-Z]+$/
                if (regs.test(value)) {
                    callback()
                } else {
                    return callback(new Error('账号只可为数字与字母'))
                }
            }
        }
        return {
            radio: 1,
            isTrue: true,
            loginShow: true,
            // 登录
            loginInfo: {
                username: 'thinkgem',
                password: '888888',
            },
            uesrloginInfo: {
                username: '',
                password: '',
                code: ''
            },
            loginRules: {
                username: [
                    {required: true, trigger: ['blur', 'change'], validator: funChina},
                    // {max: 11, message: '密码长度最少为11位', trigger: ['blur', 'change']}
                ],
                password: [
                    {required: true, message: '请输入密码', trigger: ['blur', 'change']},
                    // {min: 5, message: '密码长度最少为6位', trigger: ['blur', 'change']}
                ],
                code: [
                    {required: true, message: '请输入验证码', trigger: ['blur', 'change']},
                ]
            },
            // 验证码
            show: true,
            count: '',
            timer: null,
            checked:true,
            showUser: true,
            news:{},
            newsArr: [],
            arr: ['11','22']
        }
    },
    methods: {
        dateFormat(time) {
            var date=new Date(time);
            var year=date.getFullYear();
            var month= date.getMonth()+1<10 ? "0"+(date.getMonth()+1) : date.getMonth()+1;
            var day=date.getDate()<10 ? "0"+date.getDate() : date.getDate();
            var hours=date.getHours()<10 ? "0"+date.getHours() : date.getHours();
            var minutes=date.getMinutes()<10 ? "0"+date.getMinutes() : date.getMinutes();
            var seconds=date.getSeconds()<10 ? "0"+date.getSeconds() : date.getSeconds();
            // 拼接
            return year+"-"+month+"-"+day;
        },
        toggleBar(item) {
            if (item == 'user') {
                this.isTrue = true
                this.showUser = true
            } else {
                window.open('http://www.haspt.com/sxsy/sxsy/login', '');
                // this.isTrue = false
                // this.showUser = false
            }
        },
        // 验证码
        getCode (formData) {
            getCodes ({tel:formData.username}).then(((res) => {
                console.log(res)
            }))
            
            // 验证码倒计时
            if (!this.timer) {
                this.count = TIME_COUNT
                this.show = false
                this.timer = setInterval(() => {
                    if (this.count > 0 && this.count <= TIME_COUNT) {
                        this.count--
                    } else {
                        this.show = true
                        clearInterval(this.timer)
                        this.timer = null
                    }
                }, 1000)
            }
        },
        // 机构登录
        submitForm (formName) {
            console.log(typeof Qs.stringify(this.loginInfo))
           this.$refs[formName].validate((valid) => {
                if (valid) {
                    getLogin(this.loginInfo).then((res) => {
                        console.log(res)
                    })
                    // this.$store.dispatch('LoginByUsername', this.loginInfo)
                    // this.$store.commit('SET_MENU', [])
                }
            })
        },
        // 用户登录
        usersubmitForm (formName) {
           this.$refs[formName].validate((valid) => {
                if (valid) {
                    getUserLogin(this.uesrloginInfo).then((res) => {
                        if (res.status == '200') {
                            this.$message({
                                showClose: true,
                                message: '登录成功！',
                                type: 'success'
                            });
                            this.$store.commit('SET_MENU', res.data)
                            this.loginShow = !this.loginShow
                        } else {
                            this.$message({
                                showClose: true,
                                message: res.msg,
                                type: 'error'
                            });
                        }
                    })
                }
            })
        },
        regist(value) {
            this.$emit('getShopCode',value)
            this.$router.push({path: '/userRegister'})
        },
        // xinwen
        getNotify() {
            getUserNotify({page:1,size: 3}).then((res) => {
                this.news = res.data.list[0]
                this.newsArr = res.data.list.slice(0,3)
                this.news.updateDate = this.dateFormat(this.news.updateDate)
                this.news.year = this.news.updateDate.slice(0,4)
                this.news.date = this.news.updateDate.slice(5,this.news.updateDate.length)
                this.newsArr.map((item) => {
                    item.updateDate = this.dateFormat(item.updateDate)
                    item.year = item.updateDate.slice(0,4)
                    item.date = item.updateDate.slice(5,this.news.updateDate.length)
                })
            })
        }
    },
    watch:{
      '$route':function(to,from){
          this.currentRouter=to.path;
      }
    },
    beforeMount(){
        this.currentRouter=this.$route.path;
    },
    mounted() {
        this.getNotify()
    },
    created() {
        if (!this.$store.state.loginData.userType) {
            this.loginShow = true
        } else {
            this.loginShow = false
        }
    }
}
</script>>
<style scoped>
.loginBox{
    width: 100%;
    height: 500px;
    background-image: url(../assets/shouye.png);
    background-repeat:no-repeat;
    background-size: cover;
}
.login{
    width: 320px;
    height: 500px;
    background-color: #fff;
    float: right;
    margin-right: 10%;
    border: none;
    border-radius: 6px 6px 0px;
    font-size: 14px;
    padding: 0 15px;
    text-align: center;
    line-height: 20px;
    font-weight: normal;
    font-style: normal;
    opacity: 1;
}
.logo{
    width: 73px;
    height: 73px;
    margin: 15px auto;
}
.logo img{
    width: 100%;
    height: 100%;
}
.loginOption{
    text-align: left;
}
.loginOption .active{
    font-family: SourceHanSansSC;
    font-weight: 400;
    font-size: 16px;
    color: rgba(25, 155, 243, 1);
    font-style: normal;
    letter-spacing: 0px;
    line-height: 25px;
    margin-right: 10px;
    margin-left: 10px;
    cursor: pointer;
}
.loginOption .isActive{
    font-family: SourceHanSansSC;
    font-weight: 400;
    font-size: 16px;
    color: rgba(139, 139, 139, 1);
    font-style: normal;
    letter-spacing: 0px;
    line-height: 25px;
    margin-right: 10px;
    margin-left: 10px;
    cursor: pointer;
}
.userLogin{
    height: 255px;
    border-bottom: 1px solid #ccc
}
/* 验证码 */
.pr {
   position: relative;
}
.code-btn {
   width: 100px;
   height: 34px;
   position: absolute;
   top: 3px;
   right: 5px;
   z-index: 222;
   color: #F5A623;
   font-size: 14px;
   border: none;
   border-left: 1px solid #bababa;
   padding-left: 16px;
   background-color: #fff;
}
.el-button--text{
    padding: 0
}
.forget{
    margin-bottom: 5px;
    display: flex;
    justify-content: space-between
}
.weChat p{
    font-weight: 400;
    font-size: 14px;
    color: rgb(16, 16, 16);
}
/* 步骤 */
.step{
    background: #fff;
    width: 100%;
}
.step p{
    font-family: SourceHanSansSC;
    font-weight: 400;
    font-size: 28px;
    color: rgb(16, 16, 16);
    margin: 30px 0 15px 0
}
.stepxian{
    width: 112px;
    background-color: rgba(25,155,243,1);
    height: 2px;
    margin: auto
}
.stepImg{
    padding: 50px 10%;
}
.stepImg img{
    width: 100%
}
.news{
    background-color: rgb(246, 246, 246);
    height: 430px;
    padding-top: 30px;
    padding-bottom: 30px;
}
.newsBottom{
    /* width: 100%; */
    margin-left: 10%;
    margin-right: 10%;
    margin-top: 30px;
    margin-bottom: 30px
}
.newsLeft{
    width: 50%;
    float: left;
}
.newsLeft .newsImg {
    width: 100%;
    height: 180px;
    background-image: url(../assets/news.png);
    background-repeat:no-repeat;
    background-size: cover;
}
.newsTitle p{
    text-align: left;
    font-family: SourceHanSansSC;
    font-weight: 400;
    font-size: 18px;
    color: rgb(16, 16, 16);
}
.newsTitle span{
    float: right;
    color: rgb(174, 174, 174);
    font-size: 18px;
}
.newsRight{
    width: 43%;
    float: right;
    border-top: 1px solid #ccc;
    margin-left: 50px
}
.newsRight .newsCoent{
    margin-top: 15px;
    height: 90px;
    border-bottom: 1px solid #ccc;
}
.newsRight .coentRight{
    border: none;
    text-align: left
}
.newsRight .coentRight p{
    font-family: SourceHanSansSC;
    font-weight: 400;
    font-size: 18px;
    color: rgb(16, 16, 16);
    margin: 6px 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
.newsRight .coentRight span {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
}
.coentLeft{
    width: 20%;
    float: left;
}
.coentLeft h1{
    font-family: SourceHanSansSC;
    font-weight: 400;
    font-size: 28px;
    color: rgb(16, 16, 16);
    margin: 0;
    text-align: left
}
.coentLeft p{
    font-family: SourceHanSansSC;
    font-weight: 400;
    font-size: 18px;
    color: rgba(174, 174, 174, 1);
    margin: 0;
    text-align: left
}
.coentRight{
    width: 75%;
    height: 50px;
    float: left;
    text-align: left;
    padding-left: 10px;
    border-left: 1px solid #ccc;
    font-family: SourceHanSansSC;
    font-weight: 400;
    font-size: 14px;
    color: rgba(174, 174, 174, 1);
}
</style>


