<template>
  <div>
    <swiper :options="swiperOption"  ref="mySwiper">
        <!-- 添加的图片 -->
        <swiper-slide v-for="(item,index) in data" :key="index">
          <div>
            <img class="swiper-slide" :src="item.imgUrl"/>
          </div>
          <div class="swiperContent">
            <h1>{{item.title}}</h1>
            <p>{{item.content}}</p>
            <el-button>申请调解</el-button>
          </div>
        </swiper-slide>
        <div class="swiper-button-prev" slot="button-prev"></div>
        <div class="swiper-button-next" slot="button-next"></div>
    </swiper>
  </div>
</template>

<script>
export default {
  name: 'carrousel',
  data () {
    return {
        data: [
            {imgUrl: require("../assets/service.png"),title: '在线调解',content: '十大阿达湿哒哒湿哒哒十大阿达湿哒哒湿哒哒十大阿达湿哒哒湿哒哒'},
            {imgUrl: require("../assets/service.png"),title: '在线调解',content: '十大阿达湿哒哒湿哒哒十大阿达湿哒哒湿哒哒十大阿达湿哒哒湿哒哒'},
            {imgUrl: require("../assets/service.png"),title: '在线调解',content: '十大阿达湿哒哒湿哒哒十大阿达湿哒哒湿哒哒十大阿达湿哒哒湿哒哒'},
            {imgUrl: require("../assets/service.png"),title: '在线调解',content: '十大阿达湿哒哒湿哒哒十大阿达湿哒哒湿哒哒十大阿达湿哒哒湿哒哒'},
            {imgUrl: require("../assets/service.png"),title: '在线调解',content: '十大阿达湿哒哒湿哒哒十大阿达湿哒哒湿哒哒十大阿达湿哒哒湿哒哒'},
            {imgUrl: require("../assets/service.png"),title: '在线调解',content: '十大阿达湿哒十大阿达湿哒哒湿哒哒十大阿达湿哒哒湿哒哒哒湿哒哒'},
            {imgUrl: require("../assets/service.png"),title: '在线调解',content: '十大阿达湿哒哒湿哒哒十大阿达湿哒哒湿哒哒十大阿达湿哒哒湿哒哒'},
            {imgUrl: require("../assets/service.png"),title: '在线调解',content: '十大阿达湿哒哒湿哒哒十大阿达湿哒哒湿哒哒十大阿达湿哒哒湿哒哒'},
            {imgUrl: require("../assets/service.png"),title: '在线调解',content: '十大阿达湿哒哒湿哒哒十大阿达湿哒哒湿哒哒十大阿达湿哒哒湿哒哒'},
        ],
      swiperOption: {
      //可见图片张数
        slidesPerView: 5,
       // 默认选中中间一张
        centeredSlides: true,
        //无限循环
        loop: true,
        //小圆点（我此处没使用，列出供参考）
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
        //自动轮播
        autoplay: {
          stopOnLastSlide: true
        },
        //上下按钮点击轮播效果
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      }
    }
  }
}
</script>
<style scoped>
.swiperContent {
  background-color: rgb(254, 145, 80);
  font-weight: 400;
  color: rgba(255, 255, 255, 1);
  padding: 20px 0
}
.swiperContent h1{
  margin: 0;
  font-size: 18px;
}
.swiperContent p{
  font-size: 14px
}
</style>
<style>
.swiper-slide{
      transition:1s;
      transform: scale(0.85);
    }
    /* //swriper自带的类名（选中时的样式） */
.swiper-slide-active,.swiper-slide-duplicate-active {
    transform: scale(1.1);
}
</style>


